.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 8px;
  padding: 12px;
  animation: slideInLeft 0.5s ease-in-out;
}

@keyframes slideInLeft {
  from {
    transform: translateX(-20px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.delete-button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 4px 8px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #c0392b;
}

.list-text {
  flex: 1;
  font-size: 16px;
  color: #333;
}

.list-actions {
  display: flex;
  gap: 8px;
}
